.footer {
  font-family: Roboto;
  font-size: 14px;
  line-height: 1.14;
  color: #545454;
  height: 100px;
  background: #f7f7f7;
  margin-right: auto;
  margin-left: auto;
  text-align: center;
  padding-left: 15px;
  padding-right: 15px;
  height: 10em;
  display: flex;
  align-items: center;
  justify-content: center;

  @media (min-width: 768px) {
    height: 80px;
  }

  @media (min-width: 1024px) {
    max-width: 1600px;
  }
}
